.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 10vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.location-input-container{
  min-height: 10vh;
  background-color: darkgrey;
  display: flex;
  align-items: center;
  justify-content: center;
}

.location-input-container button{
  height: 30px;
  width: 70px;
}

.location-input{
  display: block;
  width: 40%;
  height: 30px;
  font-size: large;
  margin-right: 15px;
}

.weather-info-container{
  font-size: 24px;
}

.location-input::placeholder{
  color: black;
}

.error{
  font-size: 60px;
  color: red;
}

.preloader img{
  height: 100px;
  width: 100px;
}